.page {
  scroll-behavior: smooth;
  background-color: #f2f6fa;
  min-width: 320px;
  font-family: Open Sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  background: linear-gradient(#000, #004cff8b);
  padding-top: 24px;
  transition: all .3s;
  position: fixed;
  inset: 0;
  transform: translateY(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0);
}

@media (width >= 640px) {
  .page__menu-icon-wrapper {
    margin-bottom: 50px;
  }
}

.page__menu-close {
  background-image: url("close-icon.889fe7a3.svg");
  width: 24px;
  height: 24px;
  margin-left: 32px;
  display: inline-block;
  right: 0;
}

